    .wrapper{
    padding-top: 5rem;
    margin: 0 auto;
    width: 600px;
  }
  
  .heading__todolist{
    font-size: 54px;
    text-align: center;
    color: #ffb63e;
    color: #61cde9;
    color: #9bd093;
    /* color: #328be0; */
  }
  
  .ul__todolist{
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .index__todolist{
    color: #ffb63e;
  }
  
  .li__todolist{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .5rem 1rem;
    border: 1px solid #61cde9;
    border-radius: 4px;
    margin-bottom: .5rem;
    color: #328be0;
  }
  
  .input__todolist{
    margin-right: 1rem;
  }
  
  .button__todolist{
    background: red;
    border-radius: 50%;
    color: #fff;
    border: none;
  }
  
  .done{
    text-decoration: line-through;
  }
  
  .notodos__todolist {
    color: #61cde9;
    font-size: 32px;
    font-weight: bold;
    text-align: center;
  }
  
  .form__todolist{
    display: flex;
    justify-content: end;
    margin-bottom: 1rem;
  }
  
  .loader__todolist{
    display: flex;
    justify-content: center;
    margin: .5rem;
  }
  
  .lds_roller {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds_roller div {
    animation: lds_roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    transform-origin: 40px 40px;
  }
  .lds_roller div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: #61cde9;
    margin: -4px 0 0 -4px;
  }
  .lds_roller div:nth-child(1) {
    animation-delay: -0.036s;
  }
  .lds_roller div:nth-child(1):after {
    top: 63px;
    left: 63px;
  }
  .lds_roller div:nth-child(2) {
    animation-delay: -0.072s;
  }
  .lds_roller div:nth-child(2):after {
    top: 68px;
    left: 56px;
  }
  .lds_roller div:nth-child(3) {
    animation-delay: -0.108s;
  }
  .lds_roller div:nth-child(3):after {
    top: 71px;
    left: 48px;
  }
  .lds_roller div:nth-child(4) {
    animation-delay: -0.144s;
  }
  .lds_roller div:nth-child(4):after {
    top: 72px;
    left: 40px;
  }
  .lds_roller div:nth-child(5) {
    animation-delay: -0.18s;
  }
  .lds_roller div:nth-child(5):after {
    top: 71px;
    left: 32px;
  }
  .lds_roller div:nth-child(6) {
    animation-delay: -0.216s;
  }
  .lds_roller div:nth-child(6):after {
    top: 68px;
    left: 24px;
  }
  .lds_roller div:nth-child(7) {
    animation-delay: -0.252s;
  }
  .lds_roller div:nth-child(7):after {
    top: 63px;
    left: 17px;
  }
  .lds_roller div:nth-child(8) {
    animation-delay: -0.288s;
  }
  .lds_roller div:nth-child(8):after {
    top: 56px;
    left: 12px;
  }
  @keyframes lds_roller {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  .btn__form_todolist{
    background: #fff;
    border-radius: 3px;
    border: 1px solid #61cde9;
    color: #ffb63e;
  }
  
  .btn__form_todolist:hover {
    background: #ffb63e;
    color: #fff;
  }
  
  .input__form_todolist {
    background: #fff;
    border-radius: 3px;
    border: 1px solid #61cde9;
    color: #328be0;
    height: 30px;
    padding: 2px 10px;
    margin-right: .5rem;
    font-size: 20px;
  }
  
  /* .input__form_todolist {
    background: #fff;
    border: 1px solid #9bd093;
  } */

  .modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    padding-top: 5rem;
}

.modal_body {
    padding: 2rem;
    width: 400px;
    border-radius: 5px;
    background: #fff;
    height: 200px;
}

.btn__modal {
    background: #fff;
    border-radius: 3px;
    border: 1px solid #61cde9;
    color: #ffb63e;
    margin-bottom: 0;
}

.btn__modal:hover {
    background: #ffb63e;
    color: #fff;
}

.h1__modal{
    color: #9bd093;
}

.text__modal{
    color: #328be0;
}